/*=============== ALBUM ===============*/
.album {
  &__bg {
    padding: 1rem 0 7rem;
    background-color: var(--first-color);
  }
}

/*=============== CONTACTS ===============*/
.contacts {
  &__header {
    padding: 1rem 0 7rem;
    background-color: var(--first-color);
  }

  &__bg {
    padding: 1rem 0 7rem;
    background-color: var(--first-color);
  }
}



/*=============== SUBSCRIBE ===============*/
.subscribe{
  &__bg{
    background-color: var(--first-color);
    padding: 5.5rem 0;
  }
  &__title,
  &__description{
    color: var(--white-color);
  }
  &__description{
    text-align: center;
    margin-bottom: var(--mb-2-5);
  }
  &__form{
    background-color: var(--input-color);
    padding: .5rem;
    display: flex;
    justify-content: space-between;
  }
  &__input{
    width: 70%;
    padding-right: .5rem;
    background-color: var(--input-color);
    color: var(--text-color);

    &::placeholder{
      color: var(--text-color);
    }
  }
}

/*=============== PARTNERS ===============*/
.partners {
  // padding: 5rem 0;

  &__title {
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
    text-align: center;
  }

  &__container {
    grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
    justify-items: center;
    row-gap: 3.5rem;
  }

  &__content {
    padding: 10px;
  }

  &__content:hover .partners__img{
    cursor: pointer;
    filter: invert(.2);
  }

  &__img {
    width: 90px;
    filter: invert(.7);
    transition: var(--img-transition);
  }
}

/*=============== FOOTER ===============*/
.footer{
  padding: 5rem 0 2rem;
  background-color: var(--first-color-footer);

  &__social,
  h1,
  h2,
  h3 {
    color: var(--title-color-footer);
  }

  &__container {
    row-gap: 5rem;
  }

  &__content {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    row-gap: 2rem;
    margin: 0 auto;
  }

  &__title,
  &__subtitle {
    font-size: var(--h3-font-size);
  }

  &__title {
    margin-bottom: var(--mb-0-5);
  }

  &__description {
    margin-bottom: var(--mb-2);
    color: var(--text-color);
  }

  &__social {
    font-size: 1.25rem;
    margin-right: var(--mb-1-25);

    &:hover {
      color: var(--title-color);
    }
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
  }

  &__item {
    margin-bottom: var(--mb-0-75);
  }

  &__link {
    color: var(--text-color);

    &:hover {
      color: var(--white-color);
    }
  }

  &__rights {
    display: flex;
    flex-direction: column;
    row-gap: 1.5rem;
    text-align: center;
  }

  &__copy,
  &__terms-link {
    font-size: var(--small-font-size);
    color: var(--text-color-light);
  }

  &__terms {
    display: flex;
    column-gap: 1.5rem;
    justify-content: center;

    &-link:hover {
      color: var(--text-color);
    }
  }

  @media screen and (min-width: 500px) {
    &__content{
      margin: 0;
    }
  }
}

/*========== SCROLL UP ==========*/
.scrollup {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 46px;
  padding: .5rem;
  position: fixed;
  right: 1rem;
  bottom: -20%;
  background-color: var(--first-color);
  border-radius: 25px;
  box-shadow: 0px 0px 5px 5px #FFF ;
  opacity: .9;
  z-index: var(--z-tooltip);
  transition: .4s;
  cursor: pointer;

  &:hover{
    background-color: var(--first-color-alt);
    opacity: 1;
  }

  &__icon{
    color: var(--white-color);
    font-size: 1.2rem;
  }
}

/* Show scroll */
.show-scroll {
  bottom: 4rem;
}

/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: .60rem;
  background-color: var(--scroll-bar-color);
}

::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-color-light);
}

/*=============== MEDIA QUERIES ===============*/
/* For small devices */
@media screen and (max-width: 340px){
  .place{
    &__container{
      grid-template-columns: max-content;
      justify-content: center;
    }
  }

  .experience{
    &__content{
      padding: 0;
    }
    &__overlay:nth-child(1){
      width: 190px;
    }
    &__overlay:nth-child(2){
      width: 80px;
    }
  }

  .experience__img,
  .video__container{
    padding: 0;
  }
}

/* For medium devices */
@media screen and (min-width: 568px){
  .video__container{
    display: grid;
    grid-template-columns: .6fr;
    justify-content: center;
  }

  .place{
    &__container{
      grid-template-columns: repeat(3, max-content);
    }
  }

  .subscribe{
    &__form{
      width: 470px;
      margin: 0 auto;
    }
  }
}

@media screen and (min-width: 768px) {
  body{
    margin: 0;
  }

  .nav{
    height: calc(var(--header-height) + 1.5rem);

    &__link{
      font-size: 16px;
      color: var(--white-color);
      text-transform: uppercase;

      &:hover{
        color: var(--white-color);
      }
    }
    &__dark{
      position: initial;
    }
    &__menu{
      display: flex;
      column-gap: 1rem;
    }
    &__list{
      flex-direction: row;
      column-gap: 4rem;
    }
    &__toggle,
    &__close{
      display: none;
    }
  }

  .active-link::before{
    background-color: var(--white-color);
  }

  .scroll-header .nav__link{
    color: var(--text-color);
  }
  .scroll-header .active-link{
    color: var(--active-color);
  }
  .scroll-header .active-link::before{
    background-color: var(--active-color);
  }

  .scroll-header .change-theme{
    color: var(--text-color);
  }

  .section{
    padding: 10rem 0;
  }
}

/* For large devices */
@media screen and (min-width: 1024px){
  .container{
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1200px){
    .container{
        max-width: 1160px;
    }
}

/* For tall screens on mobiles y desktop*/
@media screen and (min-height: 721px){
  body{
    margin: 0;
  }
}

.cursor{
  animation: 1s blink step-end infinite
}
@keyframes blink {
  from, to { color: transparent } 50% { color:var(--white-color) }
}
@-moz-keyframes blink {
  from, to { color: transparent } 50% { color:var(--white-color) }
}
@-webkit-keyframes blink {
  from, to { color: transparent } 50% { color:var(--white-color) }
}
@-ms-keyframes blink {
  from, to { color: transparent } 50% { color:var(--white-color) }
}
@-o-keyframes blink {
  from, to { color: transparent } 50% { color:var(--white-color) }
}